<template>
    <div class="order">
        <div class="order_box">
            <div class="scroll_area">
                <div v-if="isOut" class="advice">
                    <div v-if="isFull" class="advice_text">建议你在<span>厂外等候</span></div>
                    <div v-if="!isFull" class="advice_text">厂内当前停车资源：<span>{{list.AREA_AMOUNT_USED}}/{{list.AREA_AMOUNT_TOTAL}}</span></div>
                </div>
                <div v-else-if="isPark" class="advice">
                    <div class="advice_text"> 请前往<span>{{list.AREA_NAME}}</span>等候</div>
                </div>
                <div v-else-if="isUnloading && this.isShow" class="advice">
                    <div class="advice_text">
                        请到<span>{{list.LINE_NAME}}-{{list.CUR_PLATFORM_NAME}}</span>卸货<br />停靠扫码
                    </div>
                </div>
                <div v-else-if="isUnloading && !this.isShow" class="advice">
                    <div class="advice_text"><span>卸货中</span>，扫码后离开平台</div>
                </div>
                <div v-else-if="isFinish" class="advice">
                    <div class="advice_text">卸货完毕，请<span>及时离场</span></div>
                </div>
                <div v-if="!isFinish && !isLeave" class="wait">
                    <p v-if="isPark" class="num">
                        前面还有<span v-if="isWait">{{list.WAITTING_AMOUNT}}</span>人等待
                    </p>
                    <div class="wait_box" :class={on:isPark}>
                        <div class="park park1" :class={out_bottom:isOut}>
                            <div class="park_left">
                                <span class="iconfont park_icon">&#xe60a;</span>
                                <p>驻场</p>
                            </div>
                            <div class="park_right">
                                <div class="place">
                                    <div>位置：{{list.AREA_NAME}}</div>
                                    <div>
                                        <span>{{list.AREA_AMOUNT_USED}}</span>/<em>{{list.AREA_AMOUNT_TOTAL}}</em></div>
                                </div>
                                <div class="time">预计等待：{{list.WAITTING_DURATION?formatSeconds(list.WAITTING_DURATION):"--"}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="wait_box" :class={on:isUnloading}>
                        <div class="park">
                            <div class="park_left">
                                <span class="iconfont park_icon">&#xe74d;</span>
                                <p>卸货</p>
                            </div>
                            <div class="park_right">
                                <div class="place">区域：{{list.LINE_NAME}}</div>
                                <div class="time">
                                    <!-- 可以去的所有平台 -->
                                    <div v-if="this.list.PLATFORMS && !isUnloading">平台：{{areaList}}</div>
                                    <!-- 当前要去的平台 -->
                                    <div v-if="this.list.PLATFORMS && isUnloading">平台：{{list.CUR_PLATFORM_NAME}}</div>
                                    <div v-if="this.list.PLATFORMS"><span>{{otherNumber}}</span>/<em>{{list.PLATFORM_AMOUNT}}</em></div>   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- 卸货信息列表 -->
                <div class="unload_msg">
                    <table v-if="aList.length>1">
                        <tr>
                            <td>停靠平台</td>
                            <td>停靠时间</td>
                            <td>离开时间</td>
                        </tr>
                        <tr v-for="item in aList" :key="item.PLATFORM_CODE">
                            <td>{{item.origin[0].LINE_NAME}}{{item.origin[0].PLATFORM_NAME}}</td>
                            <td>{{item.origin.length>0?getShortTime(item.origin[0].OPERATE_TIME):"--"}}</td>
                            <td>{{item.origin.length>1?getShortTime(item.origin[1].OPERATE_TIME):"--"}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Qs from 'qs'
import Vue from 'vue'
import {mapState} from 'vuex'
export default {
    name:'Order',
    props:{
        list:Object,
        aList:{
            type:Array,
            default:[]
        }
    },
    methods:{
        formatSeconds (time) {
            let min = Math.floor(time % 3600)
            let val = (Math.floor(time / 3600)?Math.floor(time / 3600) + '小时' : '') + Math.floor(min / 60) + '分钟'
            return val
        },
        getShortTime(num){
            //return num.split(' ')[1].substring(0,5);
            return num.substring(5,16).replace(/-/,"/");
        }
    },
    computed:mapState({
        otherNumber:function(){
            let num=0
            let array=this.list.PLATFORMS
            for(let i=0;i<array.length;i++){
                if(array[i].PLATFORM_FREE==0){
                    num++
                }
            }
            return num
        },
        areaList:function(){
            let areaString=''
            let areaArray=[]
            let array=this.list.PLATFORMS
            for(let i=0;i<array.length;i++){
                if(array[i].PLATFORM_FREE==0){
                    areaArray.push(array[i].PLATFORM_NAME)
                    areaString=areaArray.join(",")
                }
            }
            return areaString
        },
        isOut:function(){
            return this.list.WAYBILL_STATE<3
        },
        isPark:function(){
            return this.list.WAYBILL_STATE==3 || this.list.WAYBILL_STATE==4
        },
        isUnloading:function(){
            return this.list.WAYBILL_STATE==5
        },
        isFinish:function(){
            return this.list.WAYBILL_STATE==6
        },
        isLeave:function(){
            return this.list.WAYBILL_STATE==7
        },
        isWait:function(){
            return this.list.WAYBILL_STATE<5
        },
        isFull:function(){
            return this.list.AREA_AMOUNT_TOTAL==this.list.AREA_AMOUNT_USED
        },
        isShow:function(state){
			if(state.isShow===true && Vue.localStorage.get('isShow')!==null){
				state.isShow=JSON.parse(Vue.localStorage.get('isShow'))
			}
			return state.isShow
		}
    })
}
</script>
<style lang="stylus" scoped>
.order
    flex 1
    overflow-x hidden
    .order_box
        padding 0 0.3rem
        .unload_msg
            table
                margin-top 0.2rem
                background #fff
                border 1px solid #ccc
                tr
                    td
                        border-color #ccc
                        padding 0.2rem 0
                tr:first-child
                    td
                        font-weight bold
                        border-color #999
        .advice
            display table
            width 100%
            height 1.5rem
            background #fff
            text-align center
            font-size 0.36rem
            color #363636
            font-weight bold
            letter-spacing 1px
            margin-top 0.16rem
            .advice_text
                display table-cell
                vertical-align middle
                font-size 0.5rem
            span    
                color #cf001f
        .wait
            font-size 0.42rem
            color #363636
            background #fff
            border-radius 0.1rem
            margin-top 0.25rem
            overflow hidden
            .num 
                height 0.9rem
                line-height 0.9rem
                text-align center
                border-bottom 1px solid #eaeaea
                span 
                    font-size 0.44rem
                    font-weight bold
                    margin 0 0.06rem
            .wait_box
                .park
                    display flex
                    align-items center
                    color #979797
                    background rgba(228,228,228,0.9)
                    .park_left
                        text-align center
                        padding 0 0.26rem
                        font-size 0.36rem
                        font-weight bold
                        .park_icon
                            font-size 0.6rem
                            color #979797   
                            margin-bottom 0.06rem
                    .park_right
                        flex 1
                        div 
                            font-size 0.36rem
                        .place
                            display flex
                            align-items center
                            justify-content space-between
                            padding 0.2rem 0 0.1rem
                            border-bottom 1px solid #ccc
                            padding-right 0.2rem
                            span    
                                font-size 0.44rem
                                font-weight bold
                        em 
                            font-style normal
                            font-size 0.3rem
                            font-weight normal
                        .time
                            padding 0.1rem 0 0.2rem
                            display flex
                            align-items center
                            justify-content space-between 
                            padding-right 0.2rem
                            span    
                                font-size 0.44rem
                                font-weight bold
                .park.out_bottom
                    border-bottom 1px solid #ccc
            .wait_box.on
                .park
                    color #363636
                    background #fff
                    font-weight bold
                    border 0.1rem solid #cf001f
                    border-radius 0.1rem
                    .park_icon
                        color #363636
                    .place
                        border-color #eaeaea
        .wait_out
            .wait_box
                .park
                    color #363636
                    background #fff
                    .park_left
                        .park_icon
                            color #363636
                    .park_right
                        .place
                            border-color #eaeaea 
                .park1
                    border-bottom 1px solid #eaeaea

</style>


