<template>
    <div class="operate">
        <div v-if="isOut || isUnloading" class="btn_box">
            <div v-if="isOut" class="scan_btn red" @click="back">继续装载，返回上一步</div>
            <div v-if="isUnloading">
                <div v-if="this.isShow" class="scan_btn" @click="arrive('0')">已停靠，扫描二维码</div>
                <div v-if="!this.isShow" class="scan_btn red" @click="arrive('1')">卸货完成，离开平台扫码</div>
            </div>
        </div>
        <div class="pop" v-if="popShow || isLeave">
			<div class="pop_con">
				<div v-if="!isError" class="pop_info">
                    <span class="iconfont true_icon">&#xe60b;</span>
                    <p v-if="isLeave">您已离厂，该次配送任务结束。</p>
                    <p class="num">
                        车牌号：<span>{{dList.CAR_PLATE_NO}}</span>
                    </p>
                    <div v-if="isUnloading && !isLeave">
                        <p v-if="isStart && !hasTimes" class="num">开始卸货</p>
                        <p v-if="!isStart && !hasTimes" class="num">完成卸货，现可离场</p>
                        <p v-if="hasTimes" class="num">还有卸货次数，请继续卸货</p>
                    </div>
                </div>
                <div v-if="isError" class="pop_info">
                    <span class="iconfont error_icon">&#xe712;</span>
                    <p>{{this.errorText}}</p>
                </div>
				<div class="confirm_box">
					<div v-if="!isLeave" @click="be_sure" class="confirm">确定</div>
					<div v-if="isLeave" @click="leave_sure" class="confirm">确定</div>
				</div>
			</div>
			<div class="mask"></div>
		</div>
    </div>
</template>

<script>
import Qs from 'qs'
import Vue from 'vue'
import {mapState} from 'vuex'
export default {
    name:'operate',
    data(){
        return{
            popShow:false,
            getType:'',
            plat_no:'',
            hasTimes:false,
            isStart:true,
            isError:false,
            errorText:''
        }
    },
    props:{
        list:Object,
        dList:Object,
    },
    computed:mapState({
        isOut:function(){
            return this.list.WAYBILL_STATE<3
        },
        isUnloading:function(){
            return this.list.WAYBILL_STATE==5
        },
        isLeave:function(){
            return this.list.WAYBILL_STATE==7
        },
        isShow:function(state){
			if(state.isShow===true && Vue.localStorage.get('isShow')!==null){
				state.isShow=JSON.parse(Vue.localStorage.get('isShow'))
			}
			return state.isShow
		}
    }),
    methods:{
        arrive:function(num){
            //微信扫码功能
			wx.scanQRCode({
				needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: (res) => {
                    console.log(res)
                    this.plat_no=res.resultStr
                    let now_time=new Date().toLocaleString('chinese',{hour12:false});
                    let userName=this.$store.state.userName;
                    let data=Qs.stringify({
                        PLATFORM_NO:this.plat_no,
                        OPERATE_OPTION:num,
                        WAYBILL_CODE:Vue.localStorage.get('orderNum'),
                        CAR_PLATE_NO:Vue.localStorage.get('carNum'),
                        OPERATOR_NAME:userName,
                        OPERATOR_ID:this.Cookies.get('wxqy_userid'),
                        OPERAT_DATE:now_time
                    })
                    this.axios.post(this.api+'/lyapi/transfer/SCAN_PLATFORM_IN_OUT',data,this.sendHeaders).then(res=>{
                        console.log(res)
                        let data=res.data
                        if(data.RESULT==1){
                            this.getType=num
                            if(this.getType==0){
                                this.$store.commit('getShow',false)
                            }else{
                                this.$store.commit('getShow',true)
                                if(data.data.UNLOAD_AMOUNT==0){
                                    this.isStart=false
                                }else{
                                    this.hasTimes=true
                                }
                            }
                            this.popShow=true
                        }else{
                            this.errorText=data.MESSAGE
                            this.isError=true
                            this.popShow=true
                        }
                    })
					
				}
            })
            // if(1==1){
            //     this.getType=num
            //     if(this.getType==0){
            //         this.$store.commit('getShow',false)
            //     }else{
            //         this.$store.commit('getShow',true)
            //         if(true){
            //             this.isStart=false
            //         }else{
            //             this.hasTimes=true
            //         }
            //     }
            //     this.popShow=true
            // }else{
            //     this.errorText="出错唔"
            //     this.isError=true
            //     this.popShow=true
            // }
        },
        back(){
            this.$router.push('/detail')
        },
        be_sure(){
            this.isStart=true
            this.isError=false
            this.hasTimes=false
            if(this.getType==1){
                this.$emit('showList')
                this.$emit('reMessage')
            }
             this.popShow=false
        },
        leave_sure(){
            Vue.localStorage.remove('carNum');
            Vue.localStorage.remove('WAYBILL_STATE');
            Vue.localStorage.remove('isShow');
            Vue.localStorage.remove('isArrive');
            Vue.localStorage.remove('orderNum');
            this.$router.push('/')
        }
    }
}
</script>

<style lang="stylus" scoped>
.operate
    .btn_box
        padding: 0.4rem 0.3rem;
        background #f3f3f3
        .scan_btn
            height: 1rem;
            line-height: 1rem;
            color: #fff;
            background-color: #FF841E;
            font-size: 0.5rem;
            font-weight: bold;
            border-radius: 0.1rem;
            margin: 0 auto;
            text-align center
        .scan_btn.red 
            background #cf001f

</style>


