<template>
    <div class="process">
        <vue-element-loading :active="isShowMask" :is-full-screen="true" />
        <home-header ref="fromChild"></home-header>
        <div class="wrapper" ref="wrapper">
            <div class="content">
                <div class="pulldown-wrapper">
                    <div v-show="beforePullDown">
                        <span class="iconfont beforeLoad">&#xe684;</span>
                    </div>
                    <div class="loading" v-show="!beforePullDown">
                        <span class="iconfont beforeLoad">&#xe684;</span>
                    </div>
                </div>
                <process-basic :list="orderList" :dList="detailList"></process-basic>
                <process-order :list="orderList" :dList="detailList" :mList="messageList" :aList="arrayList"></process-order>
            </div>
        </div>
        <process-operate :list="orderList" :dList="detailList" @showList="getProcessList" @reMessage="childMethod"></process-operate>
    </div>
</template>
<script>
import HomeHeader from '@/common/Header'
import ProcessOrder from './components/Order'
import ProcessBasic from './components/Basic'
import ProcessOperate from './components/Operate'
import VueElementLoading from 'vue-element-loading'
import BScroll from '@better-scroll/core'
import PullDown from '@better-scroll/pull-down'
BScroll.use(PullDown)
import Vue from 'vue'
import Qs from 'qs'
const TIME_BOUNCE = 800
const TIME_STOP = 100
const REQUEST_TIME = 3000
const THRESHOLD = 60
const STOP = 56
let STEP = 0
export default {
    name:'Process',
    components:{
        HomeHeader,
        ProcessOrder,
        ProcessBasic,
        ProcessOperate,
        VueElementLoading
    },
    data(){
        return{
            orderList:{},
            detailList:{},
            messageList:{},
            arrayList:[],
            timer:'',
            isShowMask:true,
            beforePullDown: true
        }
    },
    methods:{
        getDetail(){
            this.axios.post('/detail/query').then(res=>{
                if(res.data.RESULT==1){
                    let data=res.data.data
                    this.detailList=data
                }
            })
        },
        async getProcessList(){
            await this.axios.post('/process/query').then(this.getProcessListSucc)
        },
        // getDetail(){
        //     let param_json={
		// 		'CAR_PLATE_NO':Vue.localStorage.get('carNum'),
		// 		'WAYBILL_CODE':Vue.localStorage.get('orderNum')
		// 	}
        //     let myData=Qs.stringify(param_json)
        //     this.axios.post(this.api+'/lyapi/transfer/ORDER_DETAIL',myData,this.sendHeaders).then(res=>{
        //         if(res.data.RESULT==1){
        //             let data=res.data.data
        //             this.detailList=data
        //         }
        //     })
        // },
        // getProcessList(){
        //     let param_json={
		// 		'CAR_PLATE_NO':Vue.localStorage.get('carNum'),
		// 		'WAYBILL_CODE':Vue.localStorage.get('orderNum')
		// 	}
        //     let myData=Qs.stringify(param_json)
        //     let qyUserid=this.Cookies.get('wxqy_userid')
        //     this.axios.post(this.api+'/lyapi/transfer/DETAIL_LINE_UP',myData,this.sendHeaders).then(this.getProcessListSucc)
        // },
        getProcessListSucc(res){
            let process_info=res.data
            if(process_info.RESULT==1){
                let data=process_info.data
                this.orderList=data
                if(data.WAYBILL_STATE>1){
                    Vue.localStorage.set('WAYBILL_STATE',data.WAYBILL_STATE);
                }
                //卸货信息排序
                let beforeData=data.TASKS
                let afterData=[]
                let tempArr=[]
                for (let i = 0; i < beforeData.length; i++) {
                    if (tempArr.indexOf(beforeData[i].PLATFORM_CODE) === -1) {
                        afterData.push({
                            PLATFORM_CODE: beforeData[i].PLATFORM_CODE,
                            origin: [beforeData[i]]
                        })
                        tempArr.push(beforeData[i].PLATFORM_CODE)
                    } else {
                        for (let j = 0; j < afterData.length; j++) {
                            if (afterData[j].PLATFORM_CODE == beforeData[i].PLATFORM_CODE) {
                                afterData[j].origin.push(beforeData[i])
                                break
                            }
                        }
                    }
                }
                function compare(property){
                    return function(a,b){
                        var value1=a[property]
                        var value2=b[property]
                        return value1-value2
                    }
                }
                for(let i=0;i<afterData.length;i++){
                    afterData[i].origin.sort(compare('OPERATE_CODE'))
                }
                this.arrayList=afterData
                console.log(this.arrayList)
            }
            this.isShowMask=false 
        },
        childMethod(){
            this.$refs.fromChild.getNews()
        },
        initBscroll(){
            this.bscroll=new BScroll(this.$refs.wrapper,{
                scrollY: true,
                bounceTime: TIME_BOUNCE,
                pullDownRefresh: {
                    threshold: THRESHOLD,
                    stop: STOP
                }
            })
            this.bscroll.on('pullingDown', this.pullingDownHandler)
            this.bscroll.on('scrollEnd', e => {
                console.log('scrollEnd')
            })
        },
        async pullingDownHandler() {
            console.log('trigger pullDown')
            this.beforePullDown = false
            await this.getProcessList()
            this.finishPullDown()
        },
        async finishPullDown() {
            const stopTime = TIME_STOP
            await new Promise(resolve => {
                setTimeout(() => {
                this.bscroll.finishPullDown()
                resolve()
                }, stopTime)
            })
            setTimeout(() => {
                this.beforePullDown = true
                this.bscroll.refresh()
            }, TIME_BOUNCE)
        }
    },
    mounted(){
        //配置微信JSSDK参数
        this.common.getsign()
        this.getDetail()
        this.getProcessList()
        this.timer = setInterval(this.getProcessList, 60000);

        this.initBscroll()
    },
    beforeDestroy() {
        clearInterval(this.timer);
    }
    
}
</script>
<style lang="stylus" scoped>
    .process
        display flex 
        flex-direction column 
        height 100%
    .wrapper
        position relative
        flex 1
        overflow-x hidden
        -webkit-overflow-scrolling: touch;
        .pulldown-wrapper
            position absolute
            width 100%
            padding 20px 0 10px
            box-sizing border-box
            transform translateY(-100%) translateZ(0)
            text-align center
            color #999
            .beforeLoad
                font-size 0.6rem
            .loading
                animation mymove .8s linear infinite
    @keyframes mymove
    {
        from {transform:rotate(0deg);}
        to {transform:rotate(360deg);}
    }
</style>


